" label=\"获取率\" :formatter=\"formatGetRate\"></el-table-column>\r\n          </template>\r\n        </data-table>\r\n      </template>\r\n    </tab-page>\r\n  </div>\r\n</template>\r\n<style scoped>\r\n  .el-form-item__content .el-button {\r\n    margin-top: 20px;\r\n  }\r\n\r\n  .el-table--border th {\r\n    text-align: center;\r\n  }\r\n\r\n  .el-table th .cell {\r\n    text-align: center;\r\n  }\r\n\r\n  .el-form--inline .el-form-item {\r\n    margin-right: 2px;\r\n  }\r\n\r\n  .el-form-item {\r\n    margin-bottom: 2px;\r\n  }\r\n\r\n  .el-dialog__footer {\r\n    padding: 10px 0px;\r\n    text-align: center;\r\n  }\r\n\r\n  .check-all-select {\r\n    padding: 10px;\r\n  }\r\n\r\n  .check-all-select span.text-all {\r\n    font-size: 14px;\r\n    font-weight: bold;\r\n  }\r\n\r\n  .check-all-select label {\r\n    float: right;\r\n  }\r\n\r\n  .mb-10 {\r\n    margin-bottom: 10px;\r\n  }\r\n</style>\r\n<script>\r\n  import common from '../../../utils/common'\r\n  import formatDate from '../../../utils/formatDate'\r\n  import DataTable from \"../../../components/DataTable\"\r\n  import TabPage from \"../../../components/TabPage\"\r\n  export default{\r\n    components: {\r\n      DataTable,\r\n      TabPage,\r\n    },\r\n    data(){\r\n      return{\r\n        queryParams:{\r\n          beginTime: '',\r\n          endTime:''\r\n        },\r\n        dataTable: {\r\n          pageSize: 1000,\r\n          pageSizes: [10, 20, 50, 100, 1000],\r\n          url: \"/web/v1/newsms/statisticbyhour\",\r\n          queryParams:{beginTime:'',endTime:''},\r\n          emptyText: '没有记录',\r\n        },\r\n      }\r\n\r\n    },\r\n    mounted(){\r\n      var defaultPage = {label: \"短信发送量查询(小时)\", name: \"messageAmountHour\", slotName: 'messageAmountHour', closable: false};\r\n      this.$refs.tabPage.$emit(\"open-tab\", defaultPage);\r\n      var myDate = new Date();\r\n      this.initBeginTime = this.dealDate(myDate.getTime());\r\n      this.initEndTime = this.dealDate(myDate.getTime()+24*60*60*1000);\r\n\r\n      this.dataTable.queryParams.beginTime =  this.initBeginTime+\" 00:00:00\";\r\n      this.dataTable.queryParams.endTime =  this.initEndTime+\" 00:00:00\";\r\n      this.queryParams.beginTime =  this.initBeginTime;\r\n    },\r\n    methods: {\r\n      onSubmit: function () {\r\n        this.dataTable.queryParams = this.queryParams;\r\n        var tag = this.warnTime(this.dataTable.queryParams.beginTime);\r\n        if (tag) {\r\n          return;\r\n        }\r\n        this.dataTable.queryParams.beginTime = this.dealTime(this.dataTable.queryParams.beginTime);\r\n        let timeStart = new Date(this.dataTable.queryParams.beginTime).getTime();\r\n        let timeEnd = timeStart+24*60*60*1000;\r\n        this.dataTable.queryParams.endTime = this.dealTime(timeEnd);\r\n        this.dataTable.url = '/web/v1/newsms/statisticbyhour';\r\n        this.$refs.dataTable.$emit('load-data');\r\n\r\n      },\r\n      warnTime(time){\r\n        if (!time) {\r\n          this.$notify({\r\n            message: '查询时间不能为空！',\r\n            type: 'warning'\r\n          });\r\n          return true;\r\n        }\r\n        return false;\r\n      },\r\n      dealTime(time){\r\n        return formatDate(new Date(time), 'yyyy-MM-dd hh:mm:ss');\r\n      },\r\n      dealDate(date){\r\n        return formatDate(new Date(date), 'yyyy-MM-dd');\r\n      },\r\n      formatterDate(row, column){\r\n        return formatDate(row.date, 'yyyy-MM-dd');\r\n      },\r\n      formatJson(filterVal, jsonData) {\r\n        return jsonData.map(v => filterVal.map(j => {\r\n          let returnValue;\r\n          if (j == 'operator') {\r\n            returnValue = v[j].name;\r\n          } else {\r\n            returnValue = v[j];\r\n          }\r\n          return returnValue;\r\n        }))\r\n      },\r\n      formatSuccessRate(row){\r\n        let result;\r\n        if(row['successRate'] == null) {\r\n            return '--';\r\n        }else{\r\n          return parseFloat(row['successRate']*100).toFixed(2)+'%';\r\n        }\r\n      },\r\n      formatGetRate(row){\r\n        if(row['getRate'] == null) {\r\n          return '--';\r\n        }else{\r\n          return parseFloat(row['getRate']*100).toFixed(2)+'%';\r\n        }\r\n      }\r\n\r\n    }\r\n  }\r\n</script>\r\n<style>\r\n  .span-style {\r\n    display: inline-block;\r\n    margin-top: 8px;\r\n    font-size: 14px;\r\n  }\r\n</style>\r\n"]}]);
